.About {
  width: 100%;
  padding: 1em;
}

.About h2 {
  color: #0e1a29;
  width: 80%;
  font-size: 1.5rem;
  padding: 1em 0;
  line-height: 1.8rem;
}

.About p {
  font-size: 0.875rem;
  color: #777;
  line-height: 1.75rem;
  margin-bottom: 3em;
}

.About > p {
  margin-bottom: 2em;
}

.About .card {
  display: flex;
  margin-bottom: 1em;
}

.About .card h3 {
  color: #0e1a29;
  margin-bottom: 0.5em;
}

.About .card figure {
  width: 120px;
}

.About .card img {
  width: 35px;
  margin: 0;
  margin-right: 1em;
}

@media (min-width: 30em) {
  .About {
    display: flex;
    justify-content: space-between;
    padding: 2em;
  }

  .About > figure {
    width: 50%;
    padding: 2em;
    margin: auto 0;
  }

  .About > div {
    width: 50%;
  }
}

@media (min-width: 50em) {
  .About {
    position: relative;
    max-width: 1920px;
    /* height: 100vh; */
    background-image: url("../assets/about_overlay.jpeg");
    background-repeat: no-repeat;
    background-position: left;
    margin-top: 170px;
    /* overflow: hidden; */
    /* z-index: 99; */
  }

  .About > div {
    width: 40%;
  }

  .About h2 {
    font-size: 3.5rem;
    line-height: 4rem;
    padding: 0.5em 0;
  }
}
