.App-Header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 65px;
  margin-bottom: 1em;
  padding-right: 1em;
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  z-index: 10;
}

.App-Header nav {
  display: flex;
}

.App-Header nav img {
  width: 36px;
  height: auto;
}

.App-Header h1 {
  display: flex;
  position: relative;
  margin: auto 0;
  width: 120px;
  height: 65px;
  background-color: #0e1a29;
  z-index: 10;
}

.App-Header h1::before {
  position: absolute;
  top: 0;
  right: 120px;
  content: "";
  width: 60px;
  height: 100%;
  background: #0e1a29;
  transform: matrix(1, 0, 0.9, 1, 150, 0);
}

.App-Header h1 span {
  display: none;
}

.App-Header h1 img {
  position: relative;
  width: auto;
  height: 45px;
  margin: auto 0;
  margin-left: 0.5em;
  z-index: 1;
}

.App-Header ul {
  position: absolute;
  top: 64px;
  left: 1em;
  right: 1em;
  background-color: #fff;
  animation: slideInDown; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 1s; /* don't forget to set a duration! */
}

.App-Header ul a {
  text-transform: capitalize;
  display: flex;
  padding: 1em;
  border-bottom: 1px solid #f7f7f7;
}

.App-Header ul a img {
  width: 10px;
  height: auto;
  margin-top: 0.2em;
  margin-left: 0.3em;
  background-color: #fff;
}

.App-Header button {
  display: none;
}

@media (min-width: 30em) {
  .App-Header {
    height: 80px;
    padding-right: 3em;
  }

  .App-Header h1 {
    width: 140px;
    height: 80px;
  }

  .App-Header h1::before {
    right: 110px;
    width: 80px;
  }

  .App-Header h1 img {
    height: 54px;
    margin-left: 0.8em;
  }

  .App-Header ul {
    top: 80px;
  }
}

@media (min-width: 50em) {
  .App-Header {
    max-width: 1920px;
    height: 80px;
    padding-right: 4em;
  }

  .App-Header h1 {
    width: 200px;
    height: 80px;
  }

  .App-Header h1::before {
    right: 110px;
    width: 80px;
  }

  .App-Header h1 img {
    margin-left: 2.5em;
  }

  .App-Header nav > img {
    display: none;
  }

  .App-Header ul {
    position: relative;
    display: flex !important;
    top: 0;
    left: 0;
    right: 0;
    margin: auto 0;
    animation: none;
  }

  .App-Header ul li {
    margin-left: 1em;
  }

  .App-Header button {
    font-size: 1rem;
    color: #fff;
    display: inline-block;
    width: 142px;
    height: 46px;
    margin: auto 0;
    margin-left: 7em;
    border: 1px solid #68d1cb;
    background-color: #68d1cb;
  }
}
