.Services {
  padding: 1em;
}

.Services h2 {
  color: #0e1a29;
  width: 80%;
  font-size: 1.5rem;
  padding: 1em 0;
  line-height: 1.8rem;
}

.Services > div {
  margin-top: 3em;
}

.Services .card {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 3em;
}

.Services .card div {
  width: 60%;
  margin: auto 0;
}

.Services .card figure {
  display: flex;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #0e1a29;
  margin: auto 0;
}

.Services .card figure img {
  width: auto;
  height: 42px;
  margin: auto;
}

.Services .card h3 {
  margin-bottom: 0.5em;
}

.Services .card ul a {
  display: flex;
  margin-bottom: 0.5em;
}

.Services .card div img {
  height: 14px;
  margin: auto 0;
}

.Services .card:nth-child(2n + 1) {
  text-align: right;
}

.Services .card:nth-child(2n + 1) ul a {
  justify-content: flex-end;
}

.Services .card:nth-child(2n + 1) div img {
  margin-left: 0.5em;
}

.Services .card:nth-child(2n + 1) figure {
  margin-left: 1em;
}

.Services .card:nth-child(2n) {
  text-align: left;
}

.Services .card:nth-child(2n) figure {
  margin-right: 1em;
}

.Services .card:nth-child(2n) div img {
  margin-right: 0.5em;
}

/* @media (min-width: 22em) {
  .Services .card figure {
    width: 100px;
    height: 100px;
  }
} */

.Services .arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  width: 8px;
  height: 8px;
  margin: auto 0;
  padding: 3px;
}

.Services .right {
  transform: rotate(-45deg);
  margin-right: 0.5em;
  -webkit-transform: rotate(-45deg);
}

.Services .left {
  margin-left: 0.5em;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

@media (min-width: 30em) {
  .Services {
    padding: 2em;
  }

  .Services > div {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .Services .card {
    width: 48%;
  }
}

@media (min-width: 50em) {
  .Services {
    max-width: 1920px;
    padding: 4em;
  }

  .Services > div {
    margin-top: 5 em;
  }

  .Services .card figure {
    width: 100px;
    height: 100px;
  }
}
