.Team {
  padding: 1em;
}

.Team > div > h2 {
  color: #0e1a29;
  width: 80%;
  font-size: 1.5rem;
  padding: 1em 0;
  line-height: 1.8rem;
}

.Team p {
  font-size: 0.875rem;
  color: #777;
  line-height: 1.5rem;
  margin-bottom: 3em;
}

.Team div div {
  display: flex;
  margin-bottom: 1em;
  height: 80px;
}

.Team div div h2:first-of-type {
  color: #68d1cb;
  width: 37%;
  font-size: 5rem;
  background: url("../assets/heading_bg.jpeg") no-repeat left;
  background-size: contain;
  border-right: 1px solid #83868c;
}

.Team div div h2:last-of-type {
  font-weight: 500;
  display: flex;
  flex-direction: column;
  padding: 0.8em 0;
  width: 63%;
}

.Team div div h2 span {
  width: 80%;
  margin: 0 auto;
}

.Team div div h2 span:first-of-type {
  color: #83868c;
}

.Team div div h2 span:last-of-type {
  color: #0e1a29;
}

@media (min-width: 30em) {
  .Team {
    display: flex;
    padding: 2em;
  }

  .Team > div {
    width: 50%;
  }

  .Team figure {
    width: 50%;
    margin: auto;
    padding: 0 2em;
  }
}

@media (min-width: 50em) {
  .Team {
    max-width: 1920px;
    background-image: url("../assets/experiance_overlay.jpeg");
    background-repeat: no-repeat;
    background-position: right;
    padding: 3em;
  }

  .Team > div {
    margin: auto 0;
  }

  .Team > div > h2 {
    width: 100%;
    font-size: 4rem;
    padding: 0.3em 0;
    line-height: 4.4rem;
  }
}
