.Projects {
  padding: 1em;
}

.Projects h2 {
  color: #0e1a29;
  font-size: 1.5rem;
  padding: 1em 0;
  line-height: 1.8rem;
}

.Projects ul {
  display: flex;
}

.Projects ul li {
  cursor: pointer;
  font-weight: 500;
  margin-right: 1em;
}

.Projects div img {
  margin-top: 1em;
}

@media (min-width: 30em) {
  .Projects {
    padding: 2em;
  }

  .Projects div {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .Projects div img {
    width: 48%;
    margin-bottom: 1em;
  }
}

@media (min-width: 50em) {
  .Projects {
    max-width: 1920px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 4em;
  }

  .Projects h2 {
    text-transform: uppercase;
    font-size: 3.8em;
  }

  .Projects ul {
    margin: auto 0;
  }

  .Projects div {
    width: 100%;
  }

  .Projects div img {
    width: 31.5%;
  }
}
