.Achievements {
  padding: 1em;
}

.Achievements .card {
  margin-bottom: 2em;
}

.Achievements .card div {
  display: flex;
  margin-top: 0.8em;
}

.Achievements .card h2 {
  color: #68d1cb;
  width: 20%;
  font-size: 2.8rem;
}

.Achievements .card h4 {
  color: #83868c;
  margin-bottom: 0.5em;
}

.Achievements .card h3 {
  font-size: 1.4rem;
  color: #0e1a29;
}

@media (min-width: 30em) {
  .Achievements {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 2em;
  }

  .Achievements .card {
    width: 48%;
  }
}

@media (min-width: 50em) {
  .Achievements {
    max-width: 1920px;
    position: relative;
    margin-top: -12em;
    padding: 16em 4em 8em;
  }

  .Achievements .card {
    width: 25%;
  }

  .Achievements .card h2 {
    width: 30%;
  }
}
