.App-Banner {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100vh;
  background-color: #f7f7f7;
  overflow: hidden;
}

.App-Banner div {
  text-align: center;
  margin: auto;
  padding: 0 1.5em;
}

.App-Banner div h2 {
  color: #0e1a29;
  font-size: 1.8rem;
  line-height: 2rem;
}

.App-Banner div h2 span {
  color: #68d1cb;
}

.App-Banner div p {
  color: #777;
  line-height: 1.6rem;
  padding: 1.5em 0;
}

.App-Banner div a {
  color: #fff;
  display: inline-block;
  width: 140px;
  height: 42px;
  padding: 0.8em;
  background-color: #68d1cb;
}

@media (min-width: 30em) {
  .App-Banner {
    height: 65vh;
    padding: 3em;
  }
}

@media (min-width: 50em) {
  .App-Banner {
    max-width: 1920px;
    height: 100vh;
    position: relative;
    margin-bottom: 7em;
    background: url("../assets//banner_img.jpg") no-repeat right top;
    background-size: contain;
    overflow: hidden;
  }

  .App-Banner div {
    text-align: left;
    margin: auto 0;
    width: 50%;
  }

  .App-Banner div h2 {
    font-size: 3.8rem;
    line-height: 4rem;
  }
}
