.Testimonies {
  padding: 3em 1em;
}

.Testimonies h2 {
  color: #0e1a29;
  font-size: 1.8rem;
  padding: 1em 0;
  line-height: 2rem;
}

.Testimonies p {
  font-size: 0.875rem;
  color: #888888;
  line-height: 1.5rem;
  margin-bottom: 3em;
}

.Testimonies .card figure {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 50%;
}

.Testimonies .card div {
  text-align: center;
  margin-top: -50px;
  padding: 5em 1em 3em;
  background-color: #fff;
  box-shadow: 0px 0px 40px 0px rgba(0, 27, 94, 0.1);
  border-radius: 5px;
}

.Testimonies .card h4 {
  color: #0e1a29;
}

.Testimonies ul {
  width: 50px;
  display: flex;
  justify-content: space-between;
  margin: 1em auto;
}

.Testimonies ul li {
  width: 10px;
  height: 10px;
  background-color: #d3d3d7;
  border-radius: 50%;
}

@media (min-width: 30em) {
  .Testimonies {
    padding: 2em;
  }

  .Testimonies > div:last-of-type {
    display: flex;
    justify-content: space-between;
  }

  .Testimonies .card {
    width: 48%;
    display: inline-block !important;
  }

  .Testimonies ul {
    display: none;
  }
}

@media (min-width: 50em) {
  .Testimonies {
    max-width: 1920px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background-image: url("../assets/experiance_overlay.jpeg");
    background-repeat: no-repeat;
    background-position: right;
    padding: 4em;
  }

  .Testimonies > div {
    width: 45%;
  }

  .Testimonies > div:last-of-type {
    width: 45%;
  }

  .Testimonies h2 {
    font-size: 4.8rem;
    padding: 0.3em 0;
    line-height: 5rem;
  }
}
