.Contact {
  padding: 3em 1em;
}

.Contact h2 {
  color: #0e1a29;
  font-size: 1.8rem;
  /* padding: 1em 0; */
  line-height: 2rem;
}

.Contact footer {
  display: flex;
  margin: 5em 0 3em;
}

.Contact hgroup h4 {
  color: #888888;
  font-size: 0.875rem;
}

.Contact footer ul {
  display: flex;
  height: auto;
  margin: auto 0;
}

.Contact footer ul a {
  display: flex;
  width: 42px;
  height: 42px;
  margin-right: 0.8em;
  background-color: #68d1cb;
  border-radius: 50%;
  line-height: normal;
}

.Contact footer ul li span {
  display: flex;
  margin: auto;
}

.Contact footer ul li span img {
  width: auto;
  height: 20px;
  margin: auto;
}

.Contact footer > div:last-of-type {
  margin: auto 0;
  margin-left: 2em;
}

.Contact footer h3 {
  margin-bottom: 0.8em;
}

.Contact footer h4 {
  font-weight: normal;
  color: #888888;
  height: 42px;
  padding: 0.8em 0;
}

.Contact > div:last-of-type h3 {
  margin-bottom: 1em;
}

.Contact .card {
  display: flex;
  flex-wrap: wrap;
}

.Contact .card ul {
  width: 48%;
  margin-bottom: 1em;
}

.Contact .card ul li {
  color: #888888;
  margin-bottom: 0.5em;
}

@media (min-width: 30em) {
  .Contact {
    padding: 2em;
  }

  .Contact .card ul {
    width: 31%;
  }
}

@media (min-width: 30em) {
  .Contact {
    display: flex;
    justify-content: space-between;
    padding: 8em 4em;
  }

  .Contact .card ul {
    margin-bottom: 2em;
  }
}
